<template>
  <page
    @on-ready="onReady"
    @on-show="onShow"
    ref="page"
    class="follow-page common-page"
    :headerHeightVW="headerHeightVW"
    scrollbar
  >
    <van-nav-bar slot="header" id="header" title="问题跟进" left-arrow @click-left="onClickLeft">
    </van-nav-bar>

    <van-cell-group>
      <van-cell title="问题编号：">{{problemDetail && problemDetail.no}}</van-cell>
    </van-cell-group>

    <van-cell-group>
      <van-cell title="发生时间：">{{problemDetail && problemDetail.time}}</van-cell>
      <!-- <van-cell title="发生地点：">{{problemDetail && problemDetail.address}}</van-cell> -->
      <van-cell title="故障地点" is-link clickable @click="handleSelectLine">
        <span>{{ line && line.name }} / {{ station && station.name }}</span>
      </van-cell>
      <van-field v-model="problemDetail.address" placeholder="请输入详细地址" type="textarea" :rows="2" autosize />
      <!-- <van-cell title="问题来源：">{{problemDetail && problemDetail.source}}</van-cell> -->
      <van-cell title="问题来源" is-link clickable @click="handleSelectSource">
        <span>{{ source && source.name }}</span>
      </van-cell>
      <!-- <van-cell title="所属专业：">{{problemDetail && problemDetail.profession ? problemDetail.profession : ''}}</van-cell> -->
      <van-cell title="所属专业" is-link clickable @click="handleSelectProfession">
        <span>{{ profession && profession.name }}</span>
      </van-cell>
      <!-- <van-cell title="故障设备分类：">{{problemDetail.deviceCategory}}</van-cell> -->
      <van-cell title="故障设备分类" is-link clickable @click="handleSelectDeviceCategory">
        <span>{{ deviceCategory && deviceCategory.name }}</span>
      </van-cell>
      <!-- <van-cell title="车组号：">{{problemDetail.carNo}}</van-cell> -->
      <van-field label="车组号：" v-model="problemDetail.carNo" placeholder="请填写车组号"  />
    </van-cell-group>

    <van-cell-group>
      <van-cell class="detail-title " title="问题摘要：" />
      <van-field v-model="problemDetail.summary" placeholder="请填写问题摘要" type="textarea" :rows="2" autosize />
    </van-cell-group>

    <van-cell-group>
      <van-cell class="detail-title " title="问题简述：" />
      <van-field v-model="problemDetail.problemDesc" placeholder="请填写问题简述" type="textarea" :rows="2" autosize />
    </van-cell-group>

    <van-cell-group>
      <van-cell class="detail-title " title="现象描述：" />
      <van-field v-model="problemDetail.description" placeholder="请填写现象描述" type="textarea" :rows="2" autosize />
    </van-cell-group>

    <!-- <van-cell-group>
      <van-cell class="detail-title" title="问题摘要：" />
      <van-cell>
        <span style="color: #969799;">{{problemDetail && problemDetail.summary || '无'}}</span>
      </van-cell>
      <van-cell class="detail-title" title="现象描述：" />
      <van-cell v-if="problemDetail.description">
        <span style="color: #969799;">{{problemDetail && problemDetail.description || '无'}}</span>
      </van-cell>
      <van-field v-else v-model="problemDetail.description" placeholder="请填写现象描述" type="textarea" :rows="2" autosize />
    </van-cell-group> -->

    <van-cell-group>
      <van-cell class="required" title="问题分类" is-link clickable @click="handleSelectCategory">
        <span>{{category && category.name || '请选择'}}</span>
      </van-cell>
      <van-cell class="required" title="问题等级" is-link clickable @click="handleSelectLevel">
        <span>{{level && level.name || '请选择'}}</span>
      </van-cell>
      <!-- <van-cell title="承包商：">{{problemDetail.contractor && problemDetail.contractor.id ? problemDetail.contractor.name : '无'}}</van-cell> -->

      <van-cell title="承包商" is-link clickable @click="handleSelectContractor">
        <span>{{problemDetail.contractor && problemDetail.contractor.id ? problemDetail.contractor.name : '请选择'}}</span>
      </van-cell>

      <!-- <van-cell title="通知承包商时间：">{{problemDetail.contractorNoticeTime}}</van-cell> -->
      <van-cell title="通知承包商时间" is-link clickable @click="handleSelectNoticeTime">
        <span>{{ noticeTime ? noticeTime : '请选择' }}</span>
      </van-cell>

      <van-cell title="承包商答复时间" is-link clickable @click="handleSelectReplyTime">
        <span>{{ replyTime ? replyTime : '请选择' }}</span>
      </van-cell>
      <van-cell title="承包商开始工作时间" is-link clickable @click="handleSelectBeginTime">
        <span>{{ beginTime ? beginTime : '请选择' }}</span>
      </van-cell>
      <van-cell title="承包商完成工作时间" is-link clickable @click="handleSelectFinishTime">
        <span>{{ finishTime ? finishTime : '请选择' }}</span>
      </van-cell>
      <van-field v-model="noRepairTime" label="非维修时间：" placeholder="请输入非维修时间"  />
      <van-cell title="故障修复时间" is-link clickable @click="handleSelectRepairTime">
        <span>{{ repairTime ? repairTime : '请选择' }}</span>
      </van-cell>
    </van-cell-group>

    <van-cell-group>
      <van-cell class="detail-title required" title="初步原因分析：" />
      <van-field v-model="problemDetail.analysis" placeholder="请填写原因" type="textarea" :rows="2" autosize />
    </van-cell-group>

    <van-cell-group>
      <van-cell class="detail-title required" title="初步解决方案：" />
      <van-field v-model="problemDetail.beginSolutions" placeholder="请填写方案" type="textarea" :rows="2" autosize />
    </van-cell-group>

    <van-cell-group>
      <van-cell class="detail-title required" title="最终解决方案：" />
      <van-field v-model="problemDetail.finishSolutions" placeholder="请填写方案" type="textarea" :rows="2" autosize />
    </van-cell-group>

    <van-cell-group>
      <van-cell class="detail-title" title="备注：" />
      <van-field v-model="problemDetail.remark" placeholder="" type="textarea" :rows="2" autosize />
    </van-cell-group>

    <van-cell-group>
      <van-cell class="detail-title" title="图像："></van-cell>
      <div class="images-container">
        <div class="image-item-show" v-for="(item, index) in imageList" :key="index" @click="handlePlayImages(index)">
          
          <auto-image :src="item.url" :width="108" :height="108" style="margin-bottom: 12px;margin-left: 12px;"></auto-image>
          <div class="image-remove" @click.stop="handleImageRemove(index)"></div>
        </div>
        <div class="image-item" style="background: #fff;"  @click.stop="handlePhoto1">
          <img src="../../assets/images/image-add.png">
        </div>
      </div>
    </van-cell-group>

    <van-cell-group>
      <van-cell class="detail-title" title="视频："></van-cell>
      <div class="images-container">
        <div class="image-item-show"  v-for="(item, index) in videoList" :key="index" @click="handlePlayVideo(item)">
          <img src="../../assets/images/video-default.png" class="img-video" />
          <div class="image-remove" @click.stop="handleVideoRemove(index)"></div>
        </div>
        <div class="image-item" style="background: #fff;"  @click.stop="handlePhoto2">
          <img src="../../assets/images/image-add.png">
        </div>
      </div>
    </van-cell-group>

    <div class="blank-empty"></div>

    <template slot="blank">
      <div class="footer-action">
        <div class="action-item" @click="handleCancel">
          <i class="action-item-icon icon-xiaoxiang"></i>
          <span>取消</span>
        </div>
        <!-- <div class="action-item" @click="handleCirculation">
          <i class="action-item-icon icon-liuzhuan"></i>
          <span>流转下级</span>
        </div> -->
        <van-button class="action-item-main btn-save" color="rgba(16, 142, 233, 0.15)" @click="handleSave">保存</van-button>
        <van-button class="action-item-main" color="#108EE9" @click="handleSubmit">提交审核</van-button>
      </div>

      <van-popup 
        v-model="levelShow"
        position="bottom"  
        class="filter-popup"
        :lock-scroll="false"
        closeable
        close-icon="close"
        round
      >
        <div class="title">
          选择问题等级
        </div>
        <a-scroll ref="filterScroller" v-if="levelShow" class="filter-scroll">
          <van-radio-group v-model="levelRadio.id">
            <van-cell-group>
              <van-cell :title="item.name" :label="item.description" clickable @click="levelRadio = {...item}" v-for="(item,index) in levelOptions" :key="index">
                <van-radio slot="right-icon" :name="item.id"  />
              </van-cell>
            </van-cell-group>
          </van-radio-group>
        </a-scroll>
        <div class="footer-action">
          <van-button class="action-item-main" color="#108EE9" @click="onLevelSelect">确定</van-button>
        </div>
      </van-popup>

      <van-popup
        v-model="contractorShow"
        position="bottom"
        class="filter-popup"
        :lock-scroll="false"
        closeable
        close-icon="close"
        round
      >
        <div class="title">选择承包商</div>
        <a-scroll ref="filterScroller" v-if="contractorShow" class="filter-scroll">
          <van-radio-group v-model="contractorRadio.id">
            <van-cell-group>
              <van-cell
                :title="item.name"
                :label="item.description"
                clickable
                @click="contractorRadio = {...item}"
                v-for="(item, index) in contractorOptions"
                :key="index"
              >
                <van-radio slot="right-icon" :name="item.id" />
              </van-cell>
            </van-cell-group>
          </van-radio-group>
        </a-scroll>
        <div class="footer-action">
          <van-button class="action-item-main" color="#108EE9" @click="onContractorSelect">确定</van-button>
        </div>
      </van-popup>

      <van-popup 
        v-model="categoryShow"
        position="bottom"  
        class="filter-popup"
        :lock-scroll="false"
        closeable
        close-icon="close"
        round
      >
        <div class="title">
          选择问题分类
        </div>
        <a-scroll ref="filterScroller" v-if="categoryShow" class="filter-scroll">
          <van-radio-group v-model="categoryRadio.id">
            <van-cell-group>
              <van-cell :title="item.name" :label="item.description" clickable @click="categoryRadio = {...item}" v-for="(item,index) in categoryOptions" :key="index">
                <van-radio slot="right-icon" :name="item.id"  />
              </van-cell>
            </van-cell-group>
          </van-radio-group>
        </a-scroll>
        <div class="footer-action">
          <van-button class="action-item-main" color="#108EE9" @click="onCategorySelect">确定</van-button>
        </div>
      </van-popup>

      <van-popup v-model="replyTimeShow" position="bottom" class="time-popup" :lock-scroll="false" round>
        <van-datetime-picker
          type="datetime"
          v-model="createTime"
          @confirm="handleReplyTimeConfirm"
          @cancel="replyTimeShow = false"
        />
      </van-popup>

      <van-popup v-model="beginTimeShow" position="bottom" class="time-popup" :lock-scroll="false" round>
        <van-datetime-picker
          type="datetime"
          v-model="createTime"
          @confirm="handleBeginTimeConfirm"
          @cancel="beginTimeShow = false"
        />
      </van-popup>

      <van-popup v-model="finishTimeShow" position="bottom" class="time-popup" :lock-scroll="false" round>
        <van-datetime-picker
          type="datetime"
          v-model="createTime"
          @confirm="handleFinishTimeConfirm"
          @cancel="finishTimeShow = false"
        />
      </van-popup>

      <van-popup v-model="repairTimeShow" position="bottom" class="time-popup" :lock-scroll="false" round>
        <van-datetime-picker
          type="datetime"
          v-model="createTime"
          @confirm="handleRepairTimeConfirm"
          @cancel="repairTimeShow = false"
        />
      </van-popup>

      <van-popup v-model="noticeTimeShow" position="bottom" class="time-popup" :lock-scroll="false" round>
        <van-datetime-picker
          type="datetime"
          v-model="createTime"
          @confirm="handleNoticeTimeConfirm"
          @cancel="noticeTimeShow = false"
        />
      </van-popup>

       <van-popup
        v-model="sourceShow"
        position="bottom"
        class="filter-popup"
        :lock-scroll="false"
        closeable
        close-icon="close"
        round
      >
        <div class="title">选择问题来源</div>
        <a-scroll ref="filterScroller" v-if="sourceShow" class="filter-scroll">
          <van-radio-group v-model="sourceRadio.id">
            <van-cell-group>
              <van-cell
                :title="item.name"
                :label="item.description"
                clickable
                @click="sourceRadio = {...item}"
                v-for="(item, index) in sourceOptions"
                :key="index"
              >
                <van-radio slot="right-icon" :name="item.id" />
              </van-cell>
            </van-cell-group>
          </van-radio-group>
        </a-scroll>
        <div class="footer-action">
          <van-button class="action-item-main" color="#108EE9" @click="onSourceSelect">确定</van-button>
        </div>
      </van-popup>

      <van-popup
        v-model="professionShow"
        position="bottom"
        class="filter-popup"
        :lock-scroll="false"
        closeable
        close-icon="close"
        round
      >
        <div class="title">选择所属专业</div>
        <a-scroll ref="filterScroller" v-if="professionShow" class="filter-scroll">
          <van-radio-group v-model="professionRadio.id">
            <van-cell-group>
              <van-cell
                :title="item.name"
                :label="item.description"
                clickable
                @click="professionRadio = {...item}"
                v-for="(item, index) in professionOptions"
                :key="index"
              >
                <van-radio slot="right-icon" :name="item.id" />
              </van-cell>
            </van-cell-group>
          </van-radio-group>
        </a-scroll>
        <div class="footer-action">
          <van-button class="action-item-main" color="#108EE9" @click="onProfessionSelect">确定</van-button>
        </div>
      </van-popup>

      <van-dialog
        v-model="editShow"
        show-cancel-button
        :before-close="beforeClose"
      >
        <van-cell title="描述"></van-cell>
        <van-field
          ref="desInput"
          v-model="description"
          type="textarea"
          :clearable="true"
        />
      </van-dialog>
    </template>
  </page>
</template>

<script>
import Page from '../../components/Page'
import AScroll from '../../components/Scroll'
import navigate from '../../utils/navigate.js'
import AutoImage from '../../components/AutoImage'
import db from '../../db'
import io from '../../api'
import moment from 'moment'
import {uploadImage, uploadVideo} from '../../utils/upload.js'
import native from '../../utils/native.js'

export default {
  components: {
    Page,
    AScroll,
    AutoImage
  },
  data: () => ({
    headerHeightVW: 0,
    header: null,
    imageList: [],
    videoList: [],
    levelOptions: JSON.parse(localStorage.getItem('problem_level')),
    categoryOptions: JSON.parse(localStorage.getItem('problem_category')),
    contractorOptions: JSON.parse(localStorage.getItem('problem_contractor')),
    professionOptions: JSON.parse(localStorage.getItem('problem_profession')),
    sourceOptions: JSON.parse(localStorage.getItem('problem_source')),
    levelShow: false,
    levelRadio: null,
    level: null,
    problemDetail: {},
    categoryShow: false,
    categoryRadio: null,
    contractorShow: false,
    contractorRadio: null,
    category: null,
    editShow: false,
    description: '',
    flow: null,
    imgFile: null,
    imageFilter: null,
    videoCompression: null,
    replyTime: '',
    replyTimeShow: false,
    createTime: new Date(),
    beginTime: '',
    beginTimeShow: false,
    finishTime: '',
    finishTimeShow: false,
    repairTime: '',
    repairTimeShow: false,
    noRepairTime: '',
    noticeTime: '',
    noticeTimeShow: false,
    user: JSON.parse(localStorage.getItem('user')),
    line: null,
    station: null,
    sourceShow: false,
    sourceRadio: null,
    source: null,
    deviceCategory: null,
    professionShow: false,
    professionRadio: null,
    profession: null,
  }),
  methods: {
    onReady (header, main) {
      this.header = header

      this.headerHeightVW = (this.header.h + 1) * 100 / this.header.w

      this.id = navigate.getParam('id')

      localStorage.setItem('address_select', '')

      localStorage.setItem('category_select', '')

      this.loadProblem()
    },
    onShow () {
      if (localStorage.getItem('address_select')) {
        const address = JSON.parse(localStorage.getItem('address_select'))

        this.line = address[0]

        this.station = address[address.length - 1]
      }

      if (localStorage.getItem('category_select')) {
        const category = JSON.parse(localStorage.getItem('category_select'))

        this.deviceCategory = category[category.length - 1]
      }
    },
    loadProblem () {
      this.$toast.loading({
        duration: 0,
        loadingType: 'spinner'
      })

      io.getProblemDetail({id: this.id}).then(data => {

        db.getProblemDetail(this.id).then(detail => {
          this.problemDetail = {...data, ...detail}

          this.level = this.problemDetail.level
          this.category = this.problemDetail.category
          this.flow = this.problemDetail.processflow

          this.line = this.problemDetail.line
          this.station = this.problemDetail.station
          this.source = this.problemDetail.source
          this.profession = this.problemDetail.profession
          this.deviceCategory = this.problemDetail.deviceCategory

          this.imageList = this.problemDetail.images || []
          this.videoList = this.problemDetail.videos || []

          this.replyTime = this.problemDetail.contractorReplyTime
          this.beginTime = this.problemDetail.contractorBeginTime
          this.finishTime = this.problemDetail.contractorFinishTime
          this.repairTime = this.problemDetail.repairFinishTime
          this.noticeTime = this.problemDetail.contractorNoticeTime

          this.noRepairTime = this.problemDetail.noRepairTime

          this.$toast.clear()
        })
      }).catch(msg => {
        this.$toast(msg)
      })
    },
    handleSelectLevel () {
      this.levelShow = true
      this.levelRadio = {...this.level}
    },
    handleSelectCategory () {
      this.categoryShow = true
      this.categoryRadio = {...this.category}
    },
    onLevelSelect () {
      this.level = {...this.levelRadio}
      this.levelShow = false
    },
    onCategorySelect () {
      this.category = {...this.categoryRadio}
      this.categoryShow = false
    },
    onClickLeft () {
      navigate.back()
    },
    handlePhoto1 () {
      api.actionSheet({
        buttons: ['打开照相机', '从相册获取']
      }, (ret, err) => {
        var index = ret.buttonIndex;
        let sourceType = ''
        if (index === 1) {
          sourceType = 'camera'
        } else if (index === 2) {
          sourceType = 'album'
        }

        sourceType && api.getPicture({
          sourceType: sourceType,
          destinationType: 'url',
          preview: true,
          quality: 50,
          mediaValue: 'pic'
        }, (ret, err) => {
          // alert(JSON.stringify(ret))

          if (ret.data) {
            this.imgFile = ret.data
            this.description = ''
            this.editShow = true
            setTimeout(() => {
              this.$refs.desInput.focus()
            }, 200)
          }
        })
      })
    },

    handlePhoto2 () {
      api.actionSheet({
        buttons: ['打开照相机', '从相册获取']
      }, (ret, err) => {
        var index = ret.buttonIndex;
        let sourceType = ''
        if (index === 1) {
          sourceType = 'camera'
        } else if (index === 2) {
          sourceType = 'album'
        }

        sourceType && api.getPicture({
          sourceType: sourceType,
          destinationType: 'url',
          preview: true,
          quality: 100,
          mediaValue: 'video',
          saveToPhotoAlbum: true,
          videoQuality: 'high',
        }, (ret, err) => {

          if (ret.data) {
            // this.videoList.push({url: ret.data})

            if (api.systemType == 'ios') {
              this.$toast.loading({
                duration: 0,
                forbidClick: true,
                loadingType: 'spinner',
                message: '正在压缩...'
              })

              if (!this.videoCompression) {
                this.videoCompression = api.require('videoCompression')
              }

              this.videoCompression.compression({
                path: ret.data,
                quality: 'medium'
              },(ret1, err1) => {
                // alert(JSON.stringify(ret1))
                if (ret1.eventType == 'completed') {
                  this.videoList.push({ url: ret1.path })
                  this.$toast.clear()
                }
              })
            } else {
              this.videoList.push({ url: ret.data })
            }
          }
        })
      })
    },
    handleVideoRemove (index) {
      this.videoList.splice(index, 1)
    },

    handleImageRemove (index) {
      this.imageList.splice(index, 1)
    },

    handlePlayVideo (item) {
      api.openVideo({
        url: item.url
      })
    },

    handlePlayImages (index) {
      localStorage.setItem('photos', JSON.stringify(this.imageList))
      navigate.open('photoViewer', {index: index})
    },

    handleReplyTimeConfirm(time) {
      this.replyTime = moment(time).format('YYYY-MM-DD HH:mm')
      this.replyTimeShow = false
    },

    handleSelectReplyTime () {
      this.createTime = this.replyTime ? new Date(this.replyTime) : new Date()
      this.replyTimeShow = true
    },

    handleSelectBeginTime () {
      this.createTime = this.beginTime ? new Date(this.beginTime) : new Date()
      this.beginTimeShow = true
    },

    handleBeginTimeConfirm (time) {
      this.beginTime = moment(time).format('YYYY-MM-DD HH:mm')
      this.beginTimeShow = false
    },

    handleSelectFinishTime () {
      this.createTime = this.finishTime ? new Date(this.finishTime) : new Date()
      this.finishTimeShow = true
    },

    handleFinishTimeConfirm (time) {
      this.finishTime = moment(time).format('YYYY-MM-DD HH:mm')
      this.finishTimeShow = false
    },

    handleSelectRepairTime () {
      this.createTime = this.repairTime ? new Date(this.repairTime) : new Date()
      this.repairTimeShow = true
    },

    handleRepairTimeConfirm (time) {
      this.repairTime = moment(time).format('YYYY-MM-DD HH:mm')
      this.repairTimeShow = false
    },

    handleSelectNoticeTime () {
      this.createTime = this.noticeTime ? new Date(this.noticeTime) : new Date()
      this.noticeTimeShow = true
    },

    handleNoticeTimeConfirm (time) {
      this.noticeTime = moment(time).format('YYYY-MM-DD HH:mm')
      this.noticeTimeShow = false
    },

    handleSelectContractor () {
      this.contractorShow = true
      this.contractorRadio = {...this.problemDetail.contractor}
    },

    onContractorSelect () {
      this.problemDetail.contractor = {...this.contractorRadio}
      this.contractorShow = false
    },

    handleSelectLine() {
      navigate.open('address')
    },

    handleSelectSource() {
      this.sourceShow = true
      this.sourceRadio = { ...this.reason }
    },

    onSourceSelect() {
      this.source = { ...this.sourceRadio }
      this.sourceShow = false
    },

    handleSelectProfession() {
      this.professionShow = true
      this.professionRadio = { ...this.profession }
    },

    onProfessionSelect() {
      this.profession = { ...this.professionRadio }
      this.professionShow = false
    },

    handleSelectDeviceCategory () {
      navigate.open('deviceCategory', {profession: this.profession ? this.profession.id : null})
    },

    beforeClose(action, done) {
      if (action === 'confirm') {
        this.imageList.push({url: this.imgFile, description: this.description})
      } else {
        this.imageList.push({url: this.imgFile, description: ''})
      }
      done()
    },

    handleCancel () {
      navigate.back()
    },

    validateData () {
      // 问题分类
      const validArray = []
      if (!this.category || !this.category.id) {
        validArray.push('问题分类')
      }
      if (!this.level || !this.level.id) {
        validArray.push('问题等级')
      }
      if (!this.problemDetail.analysis) {
        validArray.push('初步原因分析')
      }
      if (!this.problemDetail.beginSolutions) {
        validArray.push('初步解决方案')
      }
      if (!this.problemDetail.finishSolutions) {
        validArray.push('最终解决方案')
      }

      return validArray
    },

    handleCirculation () {
      console.log(this.problemDetail)
      const valids = this.validateData()
      if (valids.length) {
        this.$toast(valids.join('、')+' 未填写')
        return false
      } else {
        this.$toast.loading({
          duration: 0,
          loadingType: 'spinner'
        })
        db.saveProblem({
          ...this.problemDetail,
          images: this.imageList,
          videos: this.videoList,
          category: this.category,
          level: this.level,
          contractorReplyTime: this.replyTime,
          contractorBeginTime: this.beginTime,
          contractorFinishTime: this.finishTime,
          repairFinishTime: this.repairTime,
          noRepairTime: this.noRepairTime,
          contractorNoticeTime: this.noticeTime,
        }).then(() => {
          this.$toast.clear()
          navigate.open('circulation', {id: this.id, flow: JSON.stringify(this.flow), job: 'engineer_charge', profession: this.problemDetail.professionId, status: this.problemDetail.status})
        })
      }
    },

    handleSave () {
      const valids = this.validateData()
      if (valids.length) {
        this.$toast(valids.join('、')+' 未填写')
        return false
      } else {
        this.$toast.loading({
          duration: 0,
          loadingType: 'spinner'
        })
        db.saveProblem({
