        }).then(() => {
          this.$toast.clear()
          navigate.open('circulation', {id: this.id, flow: JSON.stringify(this.flow), job: 'engineer_charge', profession: this.problemDetail.professionId, status: this.problemDetail.status})
        })

          // db.saveProblem({
